<template>
  <div class="block">
    <!--<span class="demonstration"> Hover </span>-->
    <el-carousel height="520px" arrow="always">
      <el-carousel-item>
        <div class="slide clearfloat" v-for="item in slide">
          <a href="">
            <img :src="item.img" alt="">
            <span><br>
            <span>{{ item.title1 }}</span><br>
            <span>{{ item.title2 }}</span><br>
            <em>{{ item.title3 }}</em>
          </span>
          </a>
        </div>
      </el-carousel-item>
      <el-carousel-item>
        <div class="slide clearfloat" v-for="item in slide1">
          <a href="">
            <img :src="item.img" alt="">
            <span><br>
            <span>{{ item.title1 }}</span><br>
            <span>{{ item.title2 }}</span><br>
            <em>{{ item.title3 }}</em>
          </span>
          </a>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        slide: [
          {
            img: require('../assets/pic/1.jpg'),
            title1: ' E2',
            title2: '6 ',
            title3: ' 1299'
          },
          {
            img: require('../assets/pic/2.jpg'),
            title1: 'PRO 6 Plus',
            title2: ' EP2X  12 ',
            title3: '2999 '
          },
          {
            img: require('../assets/pic/3.jpg'),
            title1: 'PRO 6s',
            title2: ' 200   12 ',
            title3: '2299'
          },
          {
            img: require('../assets/pic/6.jpg'),
            title1: ' Note5',
            title2: '16G  100  6 ',
            title3: '899 '
          },
        ],
        slide1: [
          {
            img: require('../assets/pic/5.jpg'),
            title1: 'MX 6',
            title2: ' 6 ',
            title3: '1599 '
          },
          {
            img: require('../assets/pic/4.jpg'),
            title1: ' 5s',
            title2: ' 32G  10 ',
            title3: '799 '
          },
          {
            img: require('../assets/pic/7.jpg'),
            title1: ' X',
            title2: ' EP-31  3 ',
            title3: '1699 '
          },
          {
            img: require('../assets/pic/8.jpg'),
            title1: 'PRO 6',
            title2: ' 600  64G v',
            title3: '2299 '
          },
        ],
      }
    }
  }
</script>
<style  rel="stylesheet/scss" lang="scss">
  .el-carousel{
    width: 100%;
    margin-bottom: 30px;
  }
  .el-carousel .el-carousel__container {
    .el-carousel__arrow {
      border: none;
      padding: 0;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: rgba(0, 0, 0, .2);
      color: #fff;
      position: absolute;
      top: 40%;
      z-index: 10;
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      text-align: center;
      font-size: 20px;
      &:hover {
        background: rgba(0, 0, 0, .5);

      }
    }
  }
</style>
<style scoped rel="stylesheet/scss" lang="scss">
  .slide {
    display: inline-block;
    margin: 0 auto;
    margin-right: 100px;
    a {
      display: inline-block;
      width: 234px;
      text-align: center;
      > span {
        span {
          display: inline-block;
          padding: 5px 0;
        }
      }
      em {
        color: #f0415f;
      }
    }
  }
  :last-child {
    margin-right: 0px;
  }
  .block {
    width: 1240px;
    /*height: 520px;*/
    margin: 0 auto;
  }
</style>



