<template>
  <div>
    <div class="detail">
      <section class="section">
        <a href=""> &gt; &nbsp;&nbsp;</a> E2
      </section>
      <div class="row clearfloat">
        <detail-lf></detail-lf>
        <detail-rt slot="detailRt" class="right rt">
          <div class="right">
            <div v-for="item in optionHeader" class="option_header">
              <h1>{{ item.title }} </h1>
              <p>{{ item.write }}</p>
            </div>
            <div class="option_sell">
              <dl v-for="item in optionSell" class="clearfloat">
                <dt>{{ item.title }}</dt>
                <dd class="">
                  <div class="price">
                    <em>{{ item.em }}</em>
                    <span>{{ item.span }}</span>
                  </div>
                </dd>
              </dl>
            </div>
            <div class="option_toggle">
              <dl v-for="item in optionToggle" class="clerfloat">
                <dt>{{ item.title }}</dt>
                <dd class="clerfloat">
                  <a class="mx" href="">{{ item.mx }}</a>
                  <a class="pro" href="">{{ item.pro}}</a>
                </dd>
              </dl>
            </div>
            <div class="option_set">
              <dl v-for="item in set1" class="cleearfloat">
                <dt>{{ item.title}}</dt>
                <dd><a href="">
                  <span>{{ item.span}}</span>
                </a></dd>
              </dl>
              <dl v-for="(item , index) in set2" class="cleearfloat">
                <dt>{{ item.title }}</dt>
                <dd v-for="(color,cIndex) in item.colors">
                  <a @click="selection(cIndex)"
                     :class="{detail_class :cIndex === nowIndex}"
                     href="javascript:void(0)">
                    <img src="../assets/images/color.png" alt=""><i>{{color.name}}</i></a>
                </dd>
              </dl>
              <dl v-for="item in set3" class="cleearfloat">
                <dt>{{item.title}}</dt>
                <dd v-for="(item,mIndex) in item.memory">
                  <a @click="selection(mIndex)"
                     :class="{detail_class :mIndex === nowIndex}"
                     href="javascript:void(0)">
                    <span>{{item.name}}</span>
                  </a></dd>
              </dl>
              <dl v-for="item in set4" class="cleearfloat">
                <dt>{{ item.title }}</dt>
                <dd><a href="">
                  <span> {{item.span1}}<em>{{item.em}}</em></span>
                </a></dd>
                <dd><a href="">
                  <span>{{item.span2}}<b>{{item.b}}</b></span>
                </a></dd>
              </dl>
            </div>
            <div class="option_service">
              <dl class="clearfloat">
                <dt>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; </dt>
                <dd>
                  <span>&nbsp;<i></i></span>
                  <span><i></i></span>
                  <span><i></i></span>
                  <span><i></i>7</span>
                </dd>
              </dl>
            </div>
            <div class="option_site">
              <dl class="clearfloat">
                <dt class="lf">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</dt>
                <dd>
                  <div class="site">
                    <div class="text"> </div>
                    <div class="select"></div>
                  </div>
                </dd>
              </dl>
            </div>
            <div class="option_serve">
              <dl class="clearfloat">
                <dt class="lf">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</dt>
                <dd>  </dd>
              </dl>
            </div>
            <div class="option_buy">
              <dl class="clearfloat">
                <dt class="lf">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</dt>
                <dd class="clearfloat">
                  <div class="more">
                    <input type="text" v-model="number" @keyup="fixNumber">
                    <div @click="minus" class="min">-</div>
                    <div @click="add" class="max">+</div>
                  </div>
                  <div class="buy lf">
                    <a href=""></a>
                  </div>
                  <div class="house lf">
                    <a href="">
                      <i class="iconfont icon-pingfenwujiaoxing"></i>
                    </a>
                  </div>
                </dd>
              </dl>
            </div>
          </div>
        </detail-rt>
      </div>
      <detail-con></detail-con>
    </div>
  </div>
</template>

<script>
  import detailLf from './detail/detailLf.vue'
  import detailRt from './detail/detailRt.vue'
  import detailCon from './detail/detailCon.vue'
  export default{
    components: {
      detailLf,
      detailRt,
      detailCon,
    },
    props: {
      max: {
        type: Number,
        default: 5
      },
      min: {
        type: Number,
        default: 1
      },
      color1: {},
    },
    data(){
      return {
        optionHeader: [
          {
            title: ' MX6',
            write: '  19',
          }
        ],
        optionSell: [
          {
            title: '',
            em: '',
            span: '1599.00'
          }
        ],
        optionToggle: [
          {
            title: '',
            mx: 'MX6',
            pro: 'PRO 6 Plus',
          },
        ],
        set1: [
          {
            title: '',
            span: '',
          }
        ],
        set2: [
          {
            title: '',
            colors: [
              {
                id: '1',
                name: '',
              },
              {
                id: '2',
                name: '',
              },
              {
                id: '3',
                name: '',
              },
              {
                id: '4',
                name: '',
              },
            ],
          }
        ],
        set3: [
          {
            title: '',
            memory: [
              {
                id: '11',
                name: '3GB+32GB',
              },
              {
                id: '22',
                name: '4GB+32GB',
              },
            ],
          }
        ],
        set4: [
          {
            title: '',
            span1: '',
            em: '',
            span2: ' ',
            b: '0'
          }
        ],
        tab: '',
        nowIndex: 0,
        number: this.min
      }
    },
    watch: {
      number () {
        this.$emit('on-change', this.number)
      }
    },
    methods: {
      fixNumber () {
        let fix
        if (typeof this.number === 'string') {
          fix = Number(this.number.replace(/[^0-9]/ig, ''))
        }
        else {
          fix = this.number
        }
        if (fix > this.max || fix < this.min) {
          fix = this.min
        }
        this.number = fix
      },
      minus () {
        if (this.number <= this.min) {
          return
        }
        this.number--
      },
      add () {
        if (this.number >= this.max) {
          return
        }
        this.number++
      },
//      
      selection (index) {
        this.nowIndex = index
        this.$emit('on-change', this.set2[index])
      }
    }
  }
</script>
<style>
  .row .right .option_set > dl > dd > .detail_class {
    border-color: #00c3f5;
  }
</style>
<style scoped rel="stylesheet/scss" lang="scss">
  .detail {
    width: 1240px;
    margin: 0 auto;
    .section {
      width: 1240px;
      height: 40px;
      line-height: 40px;
      text-align: left;
      > a {
        color: #999;
      }
    }
  }
</style>



