    background: #8332fb url(../assets/boon/boon_14.jpg) top center no-repeat;
    height: 430px;
    .boon_14_a {
      width: 990px;
      height: 430px;
      margin: 0 auto;
      position: relative;
      .boon_14_center {
        position: absolute;
        top: 130px;
        left: 0px;
        width: 990px;
        height: 300px;
      }
    }
  }

  .boon_15 {
    background: #8332fb url(../assets/boon/boon_15.jpg) top center no-repeat;
    height: 150px;
  }

  .boon_16 {
    background: #8332fb;
    padding-top: 10px;
    padding-bottom: 40px;
    .slide {
      width: 990px;
      margin: 0 auto;
      .title {
        margin-bottom: 18px;
        text-align: right;
        > a {
          display: inline-block;
          border: 2px solid #fff;
          color: #fff;
          background: 0 0;
          width: 110px;
          height: 30px;
          text-align: center;
          line-height: 30px;
          margin-left: 8px;
          border-radius: 20px;
          -webkit-transition: background .3s ease;
          transition: background .3s ease;
          &:hover {
            border: 2px solid #f8465b;
            background: #f8465b;
          }
        }
      }
      .bottom {
        width: 100%;
        height: 503px;
        background: url(../assets/boon/draw-bg.png) no-repeat;
      }
    }
  }

  .boon_17 {
    width: 960px;
    margin: 0 auto;
    font-size: 14px;
    color: #333;
    line-height: 2;
    p {
      span {
        font-size: 14px;
        font-weight: 700;
        font-family: Verdana, Geneva, sans-serif;
      }
    }
  }

  /**/
  .m_tb {
    position: absolute;
    top: 0;
    left: 0;
    width: 736px;
    height: 40px;
    i {
      width: 40px;
      height: 40px;
      margin-right: 134px;
    }
  }

  .m_lr {
    position: absolute;
    top: 40px;
    left: 0;
    width: 40px;
    height: 420px;
    i {
      display: inline-block;
      width: 40px;
      height: 40px;
      margin-top: 109px;
    }
  }

  .m_con ul li {
    margin: 5px;
    width: 197px;
    height: 118px;
    float: left;
    background: url(//hd.res.meizu.com/hd/custom/hd/images/draw-kw.png);
    color: #cc1f46;
    font-size: 16px;
    text-align: center;
    border: 4px solid rgba(255, 255, 255, 0);
    > img {
      width: 75px;
      height: 75px;
      padding: 10px;
    }
  }

  .margin {
    width: 736px;
    height: 503px;
    position: relative;
    .m_top {
      @extend .m_tb;
      > i {
      }
      :last-child {
        margin-right: 0px;
      }
    }
    .m_left {
      @extend .m_lr;
    }
    .m_bottom {
      @extend .m_tb;
      position: absolute;
      top: 455px;
      left: 0;
    }
    .m_right {
      @extend .m_lr;
      position: absolute;
      top: 40px;
      left: 700px;

    }
    .m_con {
      padding: 5px;
      width: 645px;
      height: 410px;
      position: absolute;
      top: 40px;
      left: 40px;
      .ul_con1 {
        li:first-child {
          background: #fff;
          border: 4px solid #f59936;
        }
      }
      .ul_con2 {
        li:nth-child(2) {
          cursor: pointer;
          background: url(../assets/boon/draw-b.png);
          > i {
            display: block;
            margin-top: 75px;
            font-size: 16px;
            color: #fff;
          }
          &:hover {
            opacity: .8;
          }
        }
      }
    }
  }

  .right {
    width: 235px;
    height: 420px;
    margin-top: 38px;
    position: relative;
    .right_b {
      > a {
        display: block;
        width: 100%;
        height: 45px;
        line-height: 42px;
        background-color: #7025A2;
        border-radius: 6px;
        text-align: center;
        font-size: 18px;
        color: #fff;
      }
      &:hover {
        opacity: .9;
      }
    }
    .right-list {
      width: 230px;
      padding: 0 15px;
      margin-top: 95px;
      height: 245px;
      overflow: hidden;
    }
  }

  .list_con {
    width: 200px;
    ul {
      > li {
        font-size: 14px;
        color: #666;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid #e2cdcd;
        span {
          float: left;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .span1 {
          width: 70px;
          display: block;
        }
        .span2 {
          width: 110px;
          display: block;
        }
      }
    }
  }

</style>



